import React, { useState,useEffect } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/styles';
import axios from "axios";
import {
  Card,
  CardHeader,
  CardContent,
  CardActions,
  Divider,
  Button,
  TextField
} from '@material-ui/core';

const useStyles = makeStyles(() => ({
  root: {}
}));

const Password = ({close}) => {

  const [passwordError,setPasswordError] = useState('')
  const [oldError,setOldError] = useState('')
  const [confError,setConfError] = useState('')
  const classes = useStyles();

  const [values, setValues] = useState({
    old: '',
    password: '',
    confirm: ''
  });
  useEffect(()=>{
    const suc = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/.test(values.password)
    setPasswordError('')
    if(!suc&&values.password.length){
      setPasswordError('пароль содержит минимум восемь символов, как минимум одна буква и одна цифра')
    }
  },[values.password])
  const handleChange = event => {
    setValues({
      ...values,
      [event.target.name]: event.target.value
    });
  };
  const setChange = async() => {
    let err = 0
    setOldError('')
    setConfError('')
    if(!values.old){
      err++
      setOldError("введите текущий пароль")
    }
    if(!values.password){
      err++
      setPasswordError("введите новый пароль пароль")
    }
    if(!values.confirm){
      err++
      setConfError("введите новый пароль еще раз пароль")
    }
    if(values.confirm!==values.password){
      err++
      setConfError("пароли не совпадают")
    }
    if(!err&&!passwordError){
      const {old,password} = values
      const res = await axios.put(`${process.env.REACT_APP_API_BASE}/user/password`, {old,password})
      close()
    }
  };
  return (
    <Card
      className={classes.root}
    >
      <form>
        <CardHeader
          subheader="Обновление пароля"
          title="Пароль"
        />
        <Divider />
        <CardContent>

          <TextField
            fullWidth
            label="Текущий пароль"
            name="old"
            onChange={handleChange}
            style={{ marginTop: '1rem' }}
            type="old"
            value={values.old}
            variant="outlined"
            helperText={oldError}
          /> 
          <TextField
            fullWidth
            label="Новый пароль"
            name="password"
            onChange={handleChange}
            type="password"
            value={values.password}
            variant="outlined"
            style={{ marginTop: '1rem' }}
            helperText={passwordError}
          />
          <TextField
            fullWidth
            label="Подтверждение нового пароля"
            name="confirm"
            onChange={handleChange}
            style={{ marginTop: '1rem' }}
            type="password"
            value={values.confirm}
            variant="outlined"
            helperText={confError}
          />
        </CardContent>
        <Divider />
        <CardActions>
          <Button
            color="primary"
            variant="outlined"
            onClick={setChange}
          >
            Обновить
          </Button>
        </CardActions>
      </form>
    </Card>
  );
};

Password.propTypes = {
  className: PropTypes.string
};

export default Password;
