import React, { useEffect } from 'react';
import Account from './Account'
import { observer, inject} from 'mobx-react';
import { Link,Redirect } from "react-router-dom";

const EditProfile = observer(({authStore, userStore}) => {
  useEffect(()=>{
    if(authStore.current_user){
      userStore.fetchUser(authStore.current_user.id)
    }
  },[authStore.current_user])
  if(!authStore.access_token){
    return <Redirect push to="/login" />
  }
  if(!authStore.current_user){
    return <h1>Loading</h1>
  }
  return(
      <div>
        <div style={{display: "flex", padding:40}}>
          <Account/>
          {/* <Settings/> */}


        </div>
      </div>
  )})


  export default inject('authStore','userStore')(EditProfile);