import React, {useEffect,useState} from 'react';
import Container from '@material-ui/core/Container';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import { ThemeProvider } from '@material-ui/core/styles';
import { makeStyles } from '@material-ui/core/styles';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import IconButton from '@material-ui/core/IconButton';
import { observer, inject} from 'mobx-react';
import axios from "axios";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
   useParams
} from "react-router-dom";
import PropTypes from 'prop-types';
import clsx from 'clsx';
import moment from 'moment';




import {
  Card,
  CardActions,
  CardContent,
  Avatar,
  Typography,
  Divider,
  Button,
  LinearProgress
} from '@material-ui/core';

const useStyles = makeStyles(theme => ({
  root: {},
  details: {
    display: 'flex'
  },
  avatar: {
    marginLeft: 'auto',
    height: 80,
    width: 80,
    // flexShrink: 0,
    // flexGrow: 0
  },
  progress: {
    marginTop: theme.spacing(2)
  },
  uploadButton: {
    background: '#000000a6'
  },
  cover: {
    width: '100%',
    height: '50%',
    position: 'relative',
    'background-size': 'cover',
    'background-repeat': 'no-repeat',
    'background-position': 'center',
    // 'background-image': props => props.url,
    '&::before': {
      top: 0,
      left: 0,
      width: '100%',
      height: '100%',
      content: " ",
      position: 'absolute',
      'background-image': 'linear-gradient(-180deg, rgba(0,0,0,0.00) 58%, rgba(0,0,0,0.32) 100%)',
    }
  },
}));




const UserCover = observer(({userStore}) => {
  const [url, setUrl] = useState('');
  let classes = useStyles();
  const setAvatar = async () => {

  }
  const setCover = async (e) => {
    const file = e.target.value
    console.log(file)
    const res = await axios.post(`${process.env.REACT_APP_API_BASE}/upload/coverImg`, {file})
  }
  useEffect(()=>{
    console.log('hi',userStore.user.coverUrl)
    if(userStore.user.coverUrl){
      setUrl(userStore.user.coverUrl)
    }
  },[userStore.user])
  return (
    <div className={classes.cover} style={{backgroundImage:`url(${url})`}}></div>
  )
})





const AccountProfile = observer(({userStore}) => {
  const [url, setUrl] = useState('');
  let classes = useStyles();
  useEffect(()=>{
    console.log('hi',userStore.user.coverUrl)
  },[userStore.user])
  const setAvatar = e => {
    const file = e.target.files[0]
    userStore.setImg(file,'avatar')
  }
  const setCover = e => {
    const file = e.target.files[0]
    userStore.setImg(file,'cover')
  }

  return (
    <Card
    className={classes.cover} 
    style={{backgroundImage:`url(${userStore.user.coverUrl})`}}
      // className={clsx(classes.root, className)}
    >
      
      <CardActions
       style={{height: 'calc(100% - 8px)','justify-content': 'space-between','align-items': 'flex-end'}}
      >
      <input
        accept="image/*"
        style={{display:'none'}}
        onChange={setAvatar}
        id="contained-button-file2"
        // multiple
        type="file"
      />
      <Button
          style={{padding:0,borderRadius:55}}
      >
        <label htmlFor="contained-button-file2">
            <Avatar
              className={classes.avatar}
              src={userStore.user.imgUrl}
            />
        </label>
      </Button>
      <input
        accept="image/*"
        style={{display:'none'}}
        onChange={setCover}
        id="contained-button-file"
        // multiple
        type="file"
      />
      <label htmlFor="contained-button-file">
        <Button
          color="primary"
          variant="text"
          component="span"
          style={{background: '#000000a6'}}
          >
            Изменить изображение
          </Button>
      </label>


      </CardActions>
    </Card>
  );
});


export default inject('userStore')(AccountProfile)
