import React, { useState,useEffect } from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/styles';
import { observer, inject} from 'mobx-react';
import axios from "axios";
import {
  Card,
  CardHeader,
  CardContent,
  CardActions,
  Divider,
  Grid,
  Button,
  TextField
} from '@material-ui/core';
import Modal from '@material-ui/core/Modal';

import Password from './../Password';
const useStyles = makeStyles(() => ({
  root: {}
}));

const AccountDetails = observer(({userStore}) => {

  useEffect(()=>{
    if(userStore.user){
      setValues(userStore.user)
    }
  },[
    userStore.user.firstName,
    userStore.user.lastName,
    userStore.user.email,
    userStore.user.username,
    userStore.user.location,
    userStore.user.gender
  ])
  const classes = useStyles();
  const [usernameError, setUsernameError] = useState('');
  const [emailError, setEmailError] = useState('');
  
  const [values, setValues] = useState({
    firstName: '',
    lastName: '',
    email: '',
    username: '',
    location: '',
    gender:0
  });

  const handleChange = event => {
    setValues({
      ...values,
      [event.target.name]: event.target.value
    });
  };
  useEffect(()=>{
    if(values){
      if(!(/^(?=.*[A-Za-z])[A-Za-z\d]{4,}$/.test(values.username))){
        setUsernameError('некоректный username')
      } else {
        setUsernameError('')
      }
      if(!values.username){
        setUsernameError('пожалуйста заполните поле')
      }
    }
  },[values.username])

  useEffect(()=>{
    if(values){
      if(!(/^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(values.email))){
        setEmailError('некоректный email')
      } else {
        setEmailError('')
      }
      if(!values.email){
        setEmailError('пожалуйста заполните поле')
      }
    }
  },[values.email])
  const setChange = async() => {
    try {
      const response = await axios.put(`${process.env.REACT_APP_API_BASE}/user/profile`,values)
      await userStore.fetchUser(userStore.user.id)
      console.log('👉 Returned data:', response.data);

    } catch (e) {
      console.log(`😱 Axios request failed: ${e}`);
    }
    console.log(values)
  };

  const genders = [
    {
      value: 0,
      label: 'не определен'
    },
    {
      value: 1,
      label: 'женский'
    },
    {
      value: 2,
      label: 'мужской'
    }
  ];

  return (
    <Card
      className={classes.root}
    >
      <form
        autoComplete="off"
        noValidate
      >
        <CardHeader
          subheader="Изменение контактной информации"
          title="Профиль"
        />
        <Divider />
        <CardContent>
          <Grid
            container
            spacing={3}
          >
            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                // helperText="Please specify the first name"
                label="Имя"
                margin="dense"
                name="firstName"
                onChange={handleChange}
                // required
                value={values.firstName}
                variant="outlined"
              />
            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                label="Фамилия"
                margin="dense"
                name="lastName"
                onChange={handleChange}
                value={values.lastName}
                variant="outlined"
              />
            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                label="Email Address"
                margin="dense"
                name="email"
                helperText={emailError}
                onChange={handleChange}
                value={values.email}
                variant="outlined"
              />
            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                label="Никнейм"
                margin="dense"
                name="username"
                helperText={usernameError}
                onChange={handleChange}
                value={values.username}
                variant="outlined"
              />
            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                label="Пол"
                margin="dense"
                name="gender"
                onChange={handleChange}
                select
                // eslint-disable-next-line react/jsx-sort-props
                SelectProps={{ native: true }}
                value={values.gender}
                variant="outlined"
              >
                {genders.map(option => (
                  <option
                    key={option.value}
                    value={option.value}
                  >
                    {option.label}
                  </option>
                ))}
              </TextField>
            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                label="Место жительства"
                margin="dense"
                name="location"
                onChange={handleChange}
                value={values.location}
                variant="outlined"
              />
            </Grid>
          </Grid>
        </CardContent>
        <Divider />
        <CardActions>
          <Button
            color="primary"
            // variant="contained"
            onClick={setChange}
          >
            Сохранить
          </Button>
          <PasswordModal/>
        </CardActions>
      </form>
    </Card>
  );
});


export default inject('userStore')(AccountDetails);




const useStylesModal = makeStyles((theme) => ({
  paper: {
    position: 'absolute',
    width: 400,
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)'
  },
}));

function PasswordModal() {
  const classes = useStylesModal();

  const [open, setOpen] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const body = (
    <div  className={classes.paper}>
      <h2 id="simple-modal-title">Text in a modal</h2>
      <p id="simple-modal-description">
        Duis mollis, est non commodo luctus, nisi erat porttitor ligula.
      </p>
      {/* <SimpleModal /> */}
    </div>
  );

  return (
    <>
      <Button
      color="default"
      onClick={handleOpen}>
        Сменить пароль
      </Button>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      >
        <div  className={classes.paper}>
          <Password close={handleClose}/>
        </div>
      </Modal>
    </>
  );
}