import React,{useState,useEffect} from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/styles';
import { observer, inject} from 'mobx-react';
import {
  Card,
  CardHeader,
  CardContent,
  CardActions,
  Grid,
  Divider,
  FormControlLabel,
  Checkbox,
  Typography,
  Button
} from '@material-ui/core';
import axios from "axios";
const useStyles = makeStyles(() => ({
  root: {},
  item: {
    display: 'flex',
    flexDirection: 'column'
  }
}));

const Notifications = observer(({userStore}) => {
  useEffect(()=>{
    if(userStore.user){
      setValue({...value,...userStore.user})
    }
  },[
    userStore.user.friendliness,
    userStore.user.showProfile,
    userStore.user.showStatus,
  ])
  const [value, setValue] = useState({
    friendliness:false,
    showProfile:false,
    showStatus:false,
    autoBookmark:localStorage.autoBookmark!=="-",
    dark:!!localStorage.dark,
  });

  const handleChange = event => {
    setValue({
      ...value,
      [event.target.name]: !value[event.target.name]
    });
  };

  const setChanges = async() => {
    console.log(value)
    try {
      const response = await axios.put(`${process.env.REACT_APP_API_BASE}/user/settings`,value)
      await userStore.fetchUser(userStore.user.id)
      console.log('👉 Returned data:', response.data);

    } catch (e) {
      console.log(`😱 Axios request failed: ${e}`);
    }
    localStorage.setItem('dark', value.dark?"+":"")
    userStore.rootStore.changeTheme(value.dark)
    localStorage.setItem('autoBookmark', value.autoBookmark?"+":"-")
  }

  const classes = useStyles();

  return (
    <Card
    >
      <form>
        <CardHeader
          subheader="Управление настройками сайта"
          title="Настройки"
        />
        <Divider />
        <CardContent>
          <Grid
            container
            spacing={6}
            wrap="wrap"
          >
            <Grid
              className={classes.item}
              item
              // md={4}
              // sm={6}
              // xs={12}
            >
              {/* <Typography
                gutterBottom
                variant="h6"
              >
                Настройки
              </Typography> */}
              <FormControlLabel
                control={
                  <Checkbox
                    onChange={handleChange}
              
                    color="primary"
                    name="showStatus"
                    checked={value.showStatus}
                  />
                }
                label="Показывать меня в сети"
              />
              <FormControlLabel
                control={
                  <Checkbox
                    onChange={handleChange}
                    color="primary"
                    name="showProfile"
                     checked={value.showProfile}
                  />
                }
                label="Показывать мой профиль"
              />
              <FormControlLabel
                control={<Checkbox
                    onChange={handleChange} name="friendliness" checked={value.friendliness} color="primary" />}
                label="Автоматически принимать запросы дружбы"
              />
              <FormControlLabel
                control={
                  <Checkbox
                    onChange={handleChange}
                    color="primary"
                    name="autoBookmark"
                     checked={value.autoBookmark}
                  />
                }
                label="Сохранять закладки"
              />
              <FormControlLabel
                control={<Checkbox
                    onChange={handleChange} name="dark" checked={value.dark} color="primary" />}
                label="Темная тема"
              />
            </Grid>
          </Grid>
        </CardContent>
        <Divider />
        <CardActions>
          <Button
            color="primary"
            variant="outlined"
            onClick={setChanges}
          >
            Применить
          </Button>
        </CardActions>
      </form>
    </Card>
  );
});


export default inject('userStore')(Notifications);
